<template>
<div class="scrollBox">
  <header-nav :title="msg"></header-nav>
  <div id="AwardFeedback">
    <div class="question">
      <div class="questionTitle">我遇到的问题</div>
      <div class="first-list tab1">
        <div class="first-quesiton" @click="setIndex(index)" :class="{active: activeIndex === index}" v-for="(item, index) in list" :key="index">{{item.title}}</div>
      </div>
      <div v-if="activeIndex" class="rect mT30"></div>
      <div class="second" v-if="activeIndex">
        <div class="questionTitle">具体分类</div>
        <div class="first-list">
          <div class="first-quesiton" :class="{active: secondIndex === index}" @click="setSecondIndex(index)" v-for="(item, index) in list[activeIndex].children" :key="index">{{item}}</div>
        </div>
      </div>
    </div>
    <div class="top bg-fff pB20">
      <div class="questionTitle">详细描述</div>
      <div class="topBox">
        <textarea placeholder="请详细描述此问题的详细场景、时间点等消息。以便我们更好地解决问题~" v-model="textareaTxt" maxlength="300" rows="9" class="my-textarea"></textarea>
        <p class="text-right count pR20 colorAF" :class="{'redNum':textareaTxt.length===300}">{{textareaTxt.length}}/300</p>
        <div class="mT20 pL30 pR30 clear">
          <div class="imgBox fl mR30" v-for="(item, index) in img" :key="index">
            <div class="close" @click="closeImg(index)">
              <img src="../../assets/img/icon-close.png" alt="">
            </div>
            <img :src="item" alt="">
          </div>
          <div class="addImg mR20 fl" v-if="img.length < 3 && commonState.login_bol">
            <input id="fileid" @change="handleFileChange" ref="inputer" type="file" />
            <label for="fileid">
              <img src="../../assets/img/movie/add_image.png" alt="">
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="rect"></div>
    <div class="mT20 qqInputBox">
      <div class="flex-row my-textarea" >
        <input type="text" maxlength="30" v-model="qqInput" placeholder="QQ/手机/邮箱（选填）">
      </div>
    </div>
    <div class="rect mT20"></div>
    <div class="btn text-center colorFFF f30 mL30 mR30 radius10" :class="{active: textareaTxt.length > 0}" @click="btn">
      确认提交
    </div>
  </div>
</div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import { mapState } from 'vuex'

export default {
  name: 'AwardFeedback',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      list: {},
      msg: '用户反馈',
      activeIndex: '',
      secondIndex: '',
      textareaTxt: '',
      img: [],       // 上传图片数组
      qqInput: ''    // 邮箱QQ或手机号
    }
  },
  methods: {
    // 获取分类
    getCatlog () {
      this.$api.get('USER_FEEDBACK', {}, res => {
        for (let i in res.data) {
          if (this.$route.query.from) {
            if (i === 'movie') {
              this.activeIndex = i
              break
            }
          } else {
            this.activeIndex = i
            break
          }
        }
        this.list = res.data
      })
    },
    // 删除图片
    closeImg (index) {
      this.img.splice(index, 1)
    },
    setIndex (index) {
      for (let i in this.list) {
        if (index === i) {
          if (this.activeIndex === index) {
            this.activeIndex = ''
          } else {
            this.activeIndex = index
          }
        }
      }
    },
    // 设置第二个
    setSecondIndex (index) {
      for (let i of this.list[this.activeIndex].children.keys()) {
        if (index === i) {
          if (this.secondIndex === index) {
            this.secondIndex = ''
          } else {
            this.secondIndex = index
          }
        }
      }
    },
    // 确定提交
    btn () {
      if (!this.textareaTxt.trim()) {
        this.$toast({
          message: '请填写您的建议',
          position: 'middle',
          duration: 1500
        })
        return
      }
      if (this.qqInput && (!this.qqInput.trim())) {
        return this.$toast('请输入联系方式')
      }
      let data = {}
      let txt = ''
      if (!this.activeIndex) {
        return this.$toast('请选择一级分类')
      } else {
        txt = this.list[this.activeIndex].title
        if (this.secondIndex) {
          txt = txt + ':' + this.list[this.activeIndex].children[this.secondIndex]
        }
      }
      data.category = txt
      data.content = this.textareaTxt.trim()
      data.img_url = this.img.toString()
      data.contact = this.qqInput.trim()
      this.$api.post('FEEDBACK', data, (res) => {
        this.$toast({
          message: '提交反馈成功',
          position: 'middle',
          duration: 1500
        })
        // this.$router.push({path: '/personal_center'})
        this.$router.go(-1)
      })
    },
    // 上传图片功能
    handleFileChange (e) {
      if (!this.$utils.isImg(e)) {
        this.$toast({
          message: '请上传图片',
          position: 'middle',
          duration: 1500
        })
        return
      }
      let file = e.target.files[0]
      let res = this.$utils.uploader(this.$utils.cache('user_token'), 'feedback', file)
      res.then((result) => {
        this.img.push(result.url)
      })
    }
  },
  mounted () {
    this.getCatlog()
  },
  components: {
    HeaderNav
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped type="text/css">
#AwardFeedback {
  height: 100vh;
  box-sizing: border-box;
  padding-bottom: 130px;
  overflow: auto;
  .rect {
    background: #f5f5f5;
    height: 20px;
  }
  .question {
    background: #fff;
    .questionTitle {
      font-size: 32px;
      line-height: 45px;
      padding: 40px 30px 30px;
    }
    .first-list {
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
      padding: 0 40px;
      .first-quesiton {
        width: 170px;
        height: 62px;
        padding: 12px 0 8px;
        margin-bottom: 20px;
        line-height: 40px;
        border: 1px solid #999;
        border-radius: 30px;
        box-sizing: border-box;
        text-align: center;
        font-size: 28px;
        &.active {
          border-color: #F4A82B;
          color: #fff;
          background: #F4A82B;
          border-radius: 30px;
        }
      }
      &.tab1 {
        margin-bottom: 20px;
        .first-quesiton {
          margin-bottom: 0;
        }
      }
    }
  }
  .top {
    padding: 0 30px;
    margin-bottom: 18px;
    .questionTitle {
      padding: 30px 0 20px;
    }
    .topBox {
      border: 1px solid #999;
      position: relative;
      border-radius: 10px;
      overflow: hidden;
      .count {
        position: absolute;
        right: 0px;
        bottom: 20px;
      }
    }
  }
  .redNum{
    color: red;
  }
  .btn{
    line-height: 80px;
    width: 470px;
    margin: 0 auto;
    background: #ED8677;
    overflow: hidden;
    margin-top: 30px;
    background: #F4A82B;
    border-radius: 40px;
  }
  .btn.active {
    background: #f65959;
  }
  .imgBox{
    width: 100px;
    height: 100px;
    position: relative;
    .close{
      position: absolute;
      width: 32px;
      height: 32px;
      background: #fff;
      border-radius: 50%;
      right: -16px;
      top: -16px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    img{
      width: 100%;
      height: 100%;
    }
  }
  .addImg{
    position: relative;
    width: 100px;
    height: 100px;
    margin-bottom: 20px;
    input {
      position: absolute;
      left: -9999px;
    }
    img{
      width: 100%;
      height: 100%;
    }
    label {
      position: absolute;
      top: 0;left: 0;right: 0;
      bottom: 0;
      z-index: 10;
    }
  }
   input::-webkit-file-upload-button {
    background: #efeeee;
    color: #333;
    border: 0;
    padding: 40px 100px;
    border-radius: 5px;
    font-size: 12px;
    box-shadow: 1px 1px 5px rgba(0,0,0,.1), 0 0 10px rgba(0,0,0,.12);
  }
  input:focus,
  textarea:focus {
    outline: none;
    resize: none;
  }
  input{
    border: none;
    width: 100%;
    height: 90px;
    line-height: 90px;
    box-sizing: border-box;
    font-size: 30px;
    text-indent: 20px;
  }
  textarea {
    width: 100%;
    box-sizing: border-box;
    border: none;
    font-size: 30px;
    padding: 20px;
  }
  .qqInputBox {
    padding: 0 30px;
    display: flex;
    align-items: center;
    .my-textarea {
      width: 100%;
      border: 1px solid #999;
      border-radius: 10px;
      overflow: hidden;
      height: 100%;
      padding: 0 20px;
      height: 90px;
      box-sizing: border-box;
      align-items: center;
      input {
        height: 50px;
        padding: 0;
        text-indent: 0;
        line-height: 50px;
      }
    }
  }
  
  textarea::-webkit-input-placeholder, input::-webkit-input-placeholder {
    color: #666;
    font-size: 30px;
    line-height: 1.4;
  }
}
</style>



